@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
nav{
  background:#468499;
}
.brand-logo{
  font-family: 'Lobster', cursive;
  font-weight: bold;
}
.collection{
  border:none;
  margin:none;
  overflow: visible;
  overflow: initial;
}
.collection .collection-item{
  margin-bottom: 10px;
  border:0;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
  border-radius:50px;
  text-transform: capitalize;
}
.collection-item.done{
  text-decoration: line-through;
  color:#999;
}

.collection-item i.material-icons{
  border-radius:50%;
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
  color:#f2f2f2;
}
.collection-item.done i{
  color:#5ac18e
}
.collection-item i.delete{
  color:rgb(153, 149, 149);
  margin-right:7px;
}
.btn-add{
  position:absolute;
  right:20px;
  bottom:20px;
  background:#468499;
}
.loading-img{
  margin-top:50px;
}
